import React, { useState } from 'react';
import { useAuth } from '../../contexts/AuthContext';

import { useNavigate } from 'react-router-dom';
import { Send, AlertCircle, CheckCircle } from 'lucide-react';

const topics = [
  'Gp  v ni dung kha hc',
  'Bo li h thng',
  'nh gi gio vin',
  'Yu cu tnh nng mi',
  'Khc'
];

export default function FeedbackPage() {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [topic, setTopic] = useState('');
  const [content, setContent] = useState('');
  const [loading, setLoading] = useState(false);
  const [done, setDone]   = useState(false);

  const handleSubmit = async e => {
    e.preventDefault();
    if (!topic || !content.trim()) return;
    setLoading(true);
    // mock API delay
    await new Promise(r => setTimeout(r, 1200));
    setLoading(false);
    setDone(true);
    // auto back after 2s
    setTimeout(() => navigate(-1), 2000);
  };

  if (done) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center px-6">
        <div className="bg-white rounded-2xl shadow-xl p-10 max-w-md w-full text-center">
          <CheckCircle className="w-16 h-16 text-green-500 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-gray-800 mb-2">Gi thnh cng!</h2>
          <p className="text-gray-600">Cm n bn  gp . H thng s quay li trang trc...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* ------- Header (re-use same style) ------- */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-5xl mx-auto px-6 py-4 flex items-center justify-between">
          <div>
            <h1 className="text-xl font-bold text-gray-800">Phn hi / nh gi</h1>
            <p className="text-sm text-gray-600">Xin cho, {user?.name}</p>
          </div>
          <button
            onClick={() => navigate(-1)}
            className="px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
          >
            Quay li
          </button>
        </div>
      </div>

      {/* ------- Form ------- */}
      <div className="max-w-3xl mx-auto px-6 py-10">
        <form onSubmit={handleSubmit} className="bg-white rounded-2xl shadow-lg p-8 space-y-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Chn ch  <span className="text-red-500">*</span>
            </label>
            <select
              value={topic}
              onChange={e => setTopic(e.target.value)}
              required
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
            >
              <option value="">-- Vui lng chn --</option>
              {topics.map(t => (
                <option key={t} value={t}>{t}</option>
              ))}
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Ni dung phn hi <span className="text-red-500">*</span>
            </label>
            <textarea
              value={content}
              onChange={e => setContent(e.target.value)}
              rows={6}
              required
              placeholder="Nhp chi tit ni dung bn mun gi..."
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none resize-none"
            />
          </div>

          <div className="flex items-start gap-2 text-sm text-gray-600">
            <AlertCircle className="w-5 h-5 text-gray-400 flex-shrink-0" />
            <span>Phn hi ca bn s c ban qun tr xem xt v phn hi trong thi gian sm nht.</span>
          </div>

          <button
            type="submit"
            disabled={loading}
            className="w-full bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white font-semibold py-3 rounded-lg transition-colors shadow-lg hover:shadow-xl flex items-center justify-center gap-2"
          >
            {loading ? (
              <>
                <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
                ang gi...
              </>
            ) : (
              <>
                <Send className="w-5 h-5" /> Gi phn hi
              </>
            )}
          </button>
        </form>
      </div>
    </div>
  );
}