import React, { useState, useEffect } from 'react';
import SchedulePicker from './SchedulePicker';
import { Plus } from 'lucide-react';
import { mockAPI } from '../../../api/mockAPI'; // mock API

export default function CourseForm({ view, course, setView, loadCourses, setSelectedCourse }) {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    maxStudents: '',
    location: '',
    startDate: '',
    endDate: '',
    subject: '',
    level: 'beginner',
    schedule: {}
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (course) {
      setFormData({
        title: course.title,
        description: course.description,
        maxStudents: course.maxStudents,
        location: course.location,
        startDate: '',
        endDate: '',
        subject: course.subject || '',
        level: course.level || 'beginner',
        schedule: course.schedule || {}
      });
    }
  }, [course]);

  const resetForm = () => {
    setFormData({
      title: '',
      description: '',
      maxStudents: '',
      location: '',
      startDate: '',
      endDate: '',
      subject: '',
      level: 'beginner',
      schedule: {}
    });
    setSelectedCourse(null);
  };

  const handleSubmit = async () => {
    if (!formData.title || !formData.description) {
      alert('Vui lng in y  thng tin kha hc');
      return;
    }

    try {
      setLoading(true);
      if (view === 'create') {
        await mockAPI.createCourse(formData);
        alert('To kha hc thnh cng!');
      } else {
        await mockAPI.updateCourse(course.id, formData);
        alert('Cp nht kha hc thnh cng!');
      }
      resetForm();
      setView('list');
      await loadCourses();
    } catch (error) {
      alert('C li xy ra');
    }
    setLoading(false);
  };

  return (
    <div className="bg-white rounded-lg shadow-sm">
      <div className="p-6 border-b">
        <h2 className="text-xl font-semibold text-gray-900">
          {view === 'create' ? 'To kha hc mi' : 'Chnh sa kha hc'}
        </h2>
      </div>

      <div className="p-6 space-y-6">
        <div className="grid grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Tn kha hc *</label>
            <input
              type="text"
              value={formData.title}
              onChange={e => setFormData({...formData, title: e.target.value})}
              placeholder="VD: Gii tch 1"
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Mn hc</label>
            <input
              type="text"
              value={formData.subject}
              onChange={e => setFormData({...formData, subject: e.target.value})}
              placeholder="VD: Ton cao cp"
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
            />
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">M t *</label>
          <textarea
            value={formData.description}
            onChange={e => setFormData({...formData, description: e.target.value})}
            placeholder="M t chi tit..."
            rows={4}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
          />
        </div>

        <div className="grid grid-cols-3 gap-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">S lng ti a</label>
            <input
              type="number"
              value={formData.maxStudents}
              onChange={e => setFormData({...formData, maxStudents: e.target.value})}
              placeholder="20"
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Ngy bt u</label>
            <input
              type="date"
              value={formData.startDate}
              onChange={e => setFormData({...formData, startDate: e.target.value})}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Ngy kt thc</label>
            <input
              type="date"
              value={formData.endDate}
              onChange={e => setFormData({...formData, endDate: e.target.value})}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
            />
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Lch hc</label>
          <SchedulePicker
            schedule={formData.schedule}
            setSchedule={(sch) => setFormData({...formData, schedule: sch})}
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">a im</label>
          <input
            type="text"
            value={formData.location}
            onChange={e => setFormData({...formData, location: e.target.value})}
            placeholder="VD: H1-201"
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Trnh </label>
          <select
            value={formData.level}
            onChange={e => setFormData({...formData, level: e.target.value})}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
          >
            <option value="beginner">C bn</option>
            <option value="intermediate">Trung cp</option>
            <option value="advanced">Nng cao</option>
          </select>
        </div>

        <div className="flex gap-4 mt-8 pt-6 border-t">
          <button
            onClick={() => { setView('list'); resetForm(); }}
            className="flex-1 bg-gray-100 hover:bg-gray-200 text-gray-700 px-6 py-3 rounded-lg font-medium"
          >
            Hy
          </button>
          <button
            onClick={handleSubmit}
            disabled={loading}
            className="flex-1 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white px-6 py-3 rounded-lg font-medium"
          >
            {loading ? 'ang x l...' : view === 'create' ? 'To kha hc' : 'Cp nht'}
          </button>
        </div>
      </div>
    </div>
  );
}
