// src/views/pages/shared/TrackingPage.js
import React, { useState } from "react";
import { useAuth } from "../../contexts/AuthContext";

export default function TrackingPage() {
  const { user } = useAuth();

  // State chnh
  const [mode, setMode] = useState(null); // "tutee" | "class"
  const [selected, setSelected] = useState(null);

  // Mock data (sau ny gi API)
  const classList = [
    { id: 1, name: "Lp Gii tch 1", tuteeCount: 12 },
    { id: 2, name: "Lp Lp trnh C++", tuteeCount: 8 },
  ];

  const tuteeList = [
    { id: 1, name: "Nguyn Vn A", class: "Gii tch 1" },
    { id: 2, name: "Trn Th B", class: "Lp trnh C++" },
  ];

  const tuteeDetails = {
    1: { name: "Nguyn Vn A", progress: "Hon thnh 70%", lastActive: "2025-10-25" },
    2: { name: "Trn Th B", progress: "Hon thnh 45%", lastActive: "2025-10-26" },
  };

  const classDetails = {
    1: {
      name: "Gii tch 1",
      tutees: [
        { name: "Nguyn Vn A", progress: "70%" },
        { name: "Phm C", progress: "60%" },
      ],
    },
    2: {
      name: "Lp trnh C++",
      tutees: [
        { name: "Trn Th B", progress: "45%" },
        { name: "L D", progress: "90%" },
      ],
    },
  };

  // 1 Cha chn loi theo di
  if (!mode) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="bg-white shadow-xl rounded-2xl p-8 w-[400px] text-center">
          <h1 className="text-xl font-semibold text-[#002855] mb-4">Chn loi theo di</h1>
          <p className="text-gray-600 mb-6">
            Vui lng chn bn mun theo di theo <b>Tutee</b> hay <b>Lp</b>
          </p>
          <div className="flex gap-4 justify-center">
            <button
              onClick={() => setMode("tutee")}
              className="bg-[#002855] text-white px-4 py-2 rounded-lg hover:bg-blue-900 transition"
            >
              Theo di Tutee
            </button>
            <button
              onClick={() => setMode("class")}
              className="bg-[#002855] text-white px-4 py-2 rounded-lg hover:bg-blue-900 transition"
            >
              Theo di Lp
            </button>
          </div>
        </div>
      </div>
    );
  }

  // 2 Chn danh sch Tutee / Lp
  if (!selected) {
    const list = mode === "tutee" ? tuteeList : classList;

    return (
      <div className="min-h-screen bg-gray-50">
        <div className="bg-white border-b shadow-sm">
          <div className="max-w-5xl mx-auto px-6 py-4 flex items-center justify-between">
            <h1 className="text-xl font-bold text-[#002855]">
              {mode === "tutee" ? "Danh sch Tutee" : "Danh sch Lp"}
            </h1>
            <button
              onClick={() => setMode(null)}
              className="text-sm text-blue-700 hover:underline"
            >
               Quay li chn loi
            </button>
          </div>
        </div>

        <div className="max-w-4xl mx-auto p-6 space-y-4">
          {list.map((item) => (
            <div
              key={item.id}
              onClick={() => setSelected(item.id)}
              className="bg-white shadow hover:shadow-md border rounded-xl p-4 cursor-pointer transition"
            >
              <h2 className="text-lg font-semibold text-gray-800">
                {item.name}
              </h2>
              <p className="text-sm text-gray-600">
                {mode === "tutee"
                  ? `Thuc lp: ${item.class}`
                  : `S lng hc vin: ${item.tuteeCount}`}
              </p>
            </div>
          ))}
        </div>
      </div>
    );
  }

  // 3 Hin th thng tin chi tit
  const detail =
    mode === "tutee" ? tuteeDetails[selected] : classDetails[selected];

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="bg-white border-b shadow-sm">
        <div className="max-w-5xl mx-auto px-6 py-4 flex items-center justify-between">
          <h1 className="text-xl font-bold text-[#002855]">
            {mode === "tutee"
              ? `Thng tin Tutee: ${detail.name}`
              : `Chi tit lp: ${detail.name}`}
          </h1>
          <button
            onClick={() => setSelected(null)}
            className="text-sm text-blue-700 hover:underline"
          >
             Quay li danh sch
          </button>
        </div>
      </div>

      <div className="max-w-4xl mx-auto p-6">
        <div className="bg-white rounded-2xl shadow-lg p-6 border border-gray-100">
          {mode === "tutee" ? (
            <>
              <p><b>Tn:</b> {detail.name}</p>
              <p><b>Tin :</b> {detail.progress}</p>
              <p><b>Hot ng gn nht:</b> {detail.lastActive}</p>
            </>
          ) : (
            <table className="w-full text-left">
              <thead>
                <tr className="border-b text-gray-700">
                  <th className="py-3">Tn hc vin</th>
                  <th className="py-3">Tin </th>
                </tr>
              </thead>
              <tbody>
                {detail.tutees.map((t, i) => (
                  <tr key={i} className="border-b text-gray-600">
                    <td className="py-3">{t.name}</td>
                    <td className="py-3">{t.progress}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
}
